import FavoriteRestaurantsIdb from '../../data/favorite-restaurants-idb';
import { createRstaurantsItemTemplate } from '../templates/template-creator';

const Favorite = {
  async render() {
    return `
        <h1 id="main">Favorite Restaurants</h1>
        `;
  },

  async afterRender() {
    const restaurants = await FavoriteRestaurantsIdb.getAllRestaurants();
    const main = document.getElementById('maincontent');
    restaurants.forEach((restaurant) => {
      const section = document.createElement('section');
      section.setAttribute('tabindex', '0');
      section.innerHTML = createRstaurantsItemTemplate(restaurant);
      main.appendChild(section);
    });
  },
};

export default Favorite;
