import RestaurantsSource from '../../data/restaurants-source';
import { createRstaurantsItemTemplate } from '../templates/template-creator';

const ListRestaurants = {
  async render() {
    return `
        <h1 id="main">Explore Restaurants</h1>
        `;
  },

  async afterRender() {
    // this function to call after render()
    const rest = await RestaurantsSource.listRestaurants();
    const main = document.getElementById('maincontent');
    rest.forEach((restaurant) => {
      const section = document.createElement('section');
      section.setAttribute('tabindex', '0');
      section.innerHTML = createRstaurantsItemTemplate(restaurant);
      main.appendChild(section);
    });
  },
};

export default ListRestaurants;
