 left;\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n}\n\nsection .deskripsi h3{\n  grid-column-start: 1;\n  grid-column-end: 3;\n  text-align: center;\n  padding: 5px 0px 5px 0px;\n}\n\nsection .deskripsi .text-deskripsi{\n  grid-column-start: 1;\n  grid-column-end: 3;\n  text-align: justify;\n}\n\nsection .deskripsi .rating-restaurant{\n  text-align: left;\n}\n\nsection .deskripsi .city-restaurant{\n  text-align: right;\n}\n\nsection button {\n  padding: 5px;\n  margin-top: 10px;\n  grid-column-start: 1;\n  grid-column-end: 3;\n  border: none;\n  height: 44px;\n  border-radius: 10px;\n  font-family: 'Rubik';\n\n\n}\nsection button:hover{\n  background-color: gray;\n  cursor: pointer;\n  transition-duration: 0.5s;\n}\n\nsection #detail-button {\n  text-decoration: none;\n  color: black;\n  padding: 12px 34%;\n}\n\nsection #back-button {\n  text-decoration: none;\n  color: black;\n  padding: 12px 48%;\n}\n\nmain section{\n  border-radius: 20px;\n}\n\nmain h1{\n  text-align: center;\n  margin-bottom: 80px;\n  font-size: 50px;\n}\n\n\n.food-name{\n  margin:10px 0px 10px 0px;\n}\n\n\n/* detail-restaurants */\n.detail-restaurants  {\n  width: 60%;\n  text-align: left;\n}\n\n.detail-container {\n  padding: 20px;\n}\n\n.detail-restaurants img {\n  height: 400px;\n}\n\n.detail-restaurants .detail-container {\n  display: grid;\n  grid-template-columns: 1fr 1fr 1fr;\n}\n\n.detail-restaurants .city-restaurant {\n  grid-column-start: 1;\n  grid-column-end: 4;\n  text-align: right;\n}\n\n.detail-restaurants .address-restaurant {\n  grid-column-start: 1;\n  grid-column-end: 4;\n  text-align: right;\n}\n\n.detail-restaurants .food-name {\n  grid-column-start: 1;\n  grid-column-end: 4;\n  text-align: center;\n}\n\n.detail-restaurants .text-deskripsi {\n  grid-column-start: 1;\n  grid-column-end: 4;\n  text-align: justify;\n  margin-bottom: 20px;\n}\n\n.detail-restaurants h3 {\n  grid-column-start: 1;\n  grid-column-end: 4;\n}\n\n.detail-restaurants #list-foods {\n  grid-column-start: 1;\n  grid-column-end: 2;\n}\n\n.detail-restaurants button {\n  width: 100%;\n  grid-column-start: 1;\n  grid-column-end: 4;\n}\n\n#favoriteButtonContainer {\n  grid-column-start: 1;\n  grid-column-end: 4;\n}\n\nsection .favorite{\n  background-color: lightgreen;\n}\n\nsection .favorite:hover{\n  background-color: rgb(96, 192, 96);\n  transition-duration: 0.5s;\n}\n\nsection .un-favorite{\n  background-color: lightsalmon;\n}\n\nsection .un-favorite:hover{\n  background-color: rgb(203, 119, 86);\n  transition-duration: 0.5s;\n}\n\n\n.detail-restaurants h3 {\n  margin-bottom: 10px;\n  text-align: center;\n  border-bottom: 2px solid black;\n  padding-bottom: 10px;\n}\n\n/* detail-restaurants */\n\n/* costumer review */\n.costumer-review {\n  margin: 30px 0px 10px 0px;\n  grid-column-start: 1;\n  grid-column-end: 4;\n}\n\n.costumer-review .costumer-name {\n  margin-top: 50px;\n}\n\n.costumer-review p {\n  width: 100%;\n}\n.input-review #input-submit {\n  display: flex;\n  padding: 5px;\n  margin-top: 5px;\n  border: none;\n  padding: 10px 20px;\n  text-align: center;\n  border-radius: 5px;\n  font-family: 'Rubik';\n  background-color: lightgreen;\n  color: black;\n  cursor: pointer;\n}\n.input-review #input-submit:hover {\n  background-color: rgb(96, 192, 96);\n  transition-duration: 0.5s;\n}\n.input-review #input-name {\n  margin: 10px 0px 10px 0px;\n  border: 1px solid gray;\n  padding: 5px;\n}\n.input-review textarea {\n  border: 1px solid gray;\n}\n/* main */\n\n/* footer */\nfooter{\n  text-align: center;\n  padding: 30px;\n  background-color: gainsboro;\n}\n\nfooter h4{\n  font-size: 20px;\n}\n/* footer */\n\n\n\n\n/* Media query */\n@media screen and (max-width:1500px) {\n  main img{\n    width: 100%;\n    height: 100%;\n  }\n  section{\n    width: 30%;\n    margin: 20px 80px 20px 80px;\n  }\n}\n\n\n@media screen and (max-width:1000px) {\n  main img{\n    width: 100%;\n    height: 100%;\n  }\n  \n  section{\n    width: 40%;\n    margin: 20px 80px 20px