x 0px 0px;\n    margin-left: 0px;\n  }\n  nav h1{\n    font-size: 50px;\n    color: white;\n    margin-left: 20px;\n  }\n  nav {\n    display: grid;\n    grid-template-columns: 0fr;\n    grid-template-rows: 0fr;\n    text-align: left;\n  }\n  nav ul li a{\n    color: white;\n  }\n\n  /* detail-restaurants */\n  .detail-restaurants  {\n    width: 60%;\n    text-align: left;\n  }\n\n\n  .detail-restaurants img {\n    height: 400px;\n  }\n\n  .detail-restaurants .detail-container {\n    display: grid;\n    grid-template-columns: 1fr 1fr 1fr;\n    padding: 20px;\n  }\n\n  .detail-restaurants .city-restaurant {\n    grid-column-start: 2;\n    grid-column-end: 4;\n    text-align: right;\n  }\n\n  .detail-restaurants .address-restaurant {\n    grid-column-start: 1;\n    grid-column-end: 4;\n    text-align: right;\n  }\n\n  .detail-restaurants .food-name {\n    grid-column-start: 1;\n    grid-column-end: 4;\n    text-align: center;\n  }\n\n  .detail-restaurants .text-deskripsi {\n    grid-column-start: 1;\n    grid-column-end: 4;\n    text-align: justify;\n    margin-bottom: 20px;\n  }\n\n  .detail-restaurants h3 {\n    grid-column-start: 1;\n    grid-column-end: 4;\n  }\n\n  .detail-restaurants #list-foods {\n    grid-column-start: 1;\n    grid-column-end: 2;\n  }\n\n  .detail-restaurants button {\n    grid-column-start: 1;\n    grid-column-end: 4;\n  }\n\n  .detail-restaurants h3 {\n    margin-bottom: 10px;\n  }\n\n  .input-review {\n    grid-column-start: 1;\n    grid-column-end: 4;\n    margin-top: 10px;\n  }\n\n  .input-review #input-name {\n    width: 49%;\n    margin-bottom: 5px;\n  }\n\n  .input-review textarea {\n    width:80%;\n  }\n\n  .input-review #input-submit {\n    display: flex;\n    text-align: left;\n  }\n\n  section #detail-button {\n    text-decoration: none;\n    color: black;\n    padding: 12px 23%;\n  }\n  \n  section #back-button {\n    text-decoration: none;\n    color: black;\n    padding: 12px 42%;\n  }\n\n}"],"sourceRoot":""}]);
