import RestaurantsSource from '../../data/restaurants-source';
import UrlParser from '../../routes/url-parser';
import FavoriteButtonInitiator from '../../utils/favorite-button-initiator';
import { createRestaurantsDetailTemplate } from '../templates/template-creator';

const DetailRestaurants = {
  async render() {
    return `
          <h1 id="main">Detail Restaurants</h1>
          `;
  },

  async afterRender() {
    // this function to call after render()
    const url = UrlParser.parseActiveUrlWithoutCombiner();
    const restaurantDetail = await RestaurantsSource.detailRestaaurants(url.id);
    const restaurantDrinks = restaurantDetail.menus.drinks;
    const restaurantFoods = restaurantDetail.menus.foods;
    const restaurantReviews = restaurantDetail.customerReviews;
    const main = document.querySelector('#maincontent');
    const section = document.createElement('section');

    section.setAttribute('tabindex', '0');
    section.setAttribute('class', 'detail-restaurants');

    section.innerHTML = createRestaurantsDetailTemplate(restaurantDetail);
    main.appendChild(section);

    // create list restaurant drink menus
    restaurantDrinks.forEach((drink) => {
      const listDrinks = document.createElement('li');
      listDrinks.innerHTML = drink.name;
      document.querySelector('#list-drinks').appendChild(listDrinks);
    });

    // create list restaurant food menus
    restaurantFoods.forEach((food) => {
      const listFoods = document.createElement('li');
      listFoods.innerHTML = food.name;
      document.querySelector('#list-foods').appendChild(listFoods);
    });

    // create restaurant customer review
    restaurantReviews.forEach((review) => {
      const customerName = document.createElement('h5');
      const reviewDate = document.createElement('p');
      const customerReview = document.createElement('p');
      customerName.setAttribute('class', 'costumer-name');
      customerName.innerHTML = `${review.name}:`;
      reviewDate.innerHTML = review.date;
      customerReview.innerHTML = review.review;
      document.querySelector('#costumer-review').appendChild(customerName);
      document.querySelector('#costumer-review').appendChild(reviewDate);
      document.querySelector('#costumer-review').appendChild(customerReview);
    });

    FavoriteButtonInitiator.init({
      favoriteButtonContainer: document.querySelector('#favoriteButtonContainer'),
      restaurants: {
        id: restaurantDetail.id,
        name: restaurantDetail.name,
        rating: restaurantDetail.rating,
        city: restaurantDetail.city,
        pictureId: restaurantDetail.pictureId,
      },
    });
  },
};

export default DetailRestaurants;
