import CONFIG from '../../globals/config';

const createRestaurantsDetailTemplate = (restaurant) => `
    <div class="detail-restaurant">
        <img src="${CONFIG.BASE_URL_IMAGE}${restaurant.pictureId}" alt="Menu Makanan ${restaurant.name}">
    </div>
    <div class="detail-container">
        <p tabindex="0" class="rating-restaurant">Rating: ${restaurant.rating}</p>
        <p tabindex="0" class="city-restaurant">${restaurant.city}</p>
        <p tabindex="0" class="address-restaurant">${restaurant.address}</p>
        <h3 tabindex="0" class="food-name">${restaurant.name}</h3>
        <p tabindex="0" class="text-deskripsi">Lorem ipsum dolor sit, amet consectetur adipisicing elit. Sit, dolorum ratione debitis earum fugit veniam vitae consequatur corporis ullam cupiditate nam ad, fuga inventore quos velit beatae, voluptatibus aliquid sed.</p>
        <h3>Menu Restaurant</h3>
        <h4 id="foods-tittle">Menu Makanan</h4>
        <h4 id="drinks-tittle">Menu Minuman</h4>
        <ul id="list-foods">  
        </ul>
        <ul id="list-drinks">
        </ul>
        <div class="input-review">
            <p>Tambahkan Review</p>
            <input type="text" name="name" id="input-name" placeholder="masukkan nama">
            <textarea name="textarea id="" cols="30" rows="5"></textarea>
            <input type="submit" value="Kirim" id="input-submit">
        </div>
        <div id="costumer-review">
            <h4>Costumer Review</h4>
        </div>
        <div id="favoriteButtonContainer">    
        </div>
        <button tabindex="-1" ><a  id="back-button" href="/#/list-restaurants">Back</a></button>    
    </div>
`;

const createRstaurantsItemTemplate = (restaurant) => `
    <div class="menu">
        <img src="${CONFIG.BASE_URL_IMAGE}${restaurant.pictureId}" alt="Menu Makanan ${restaurant.name}">
    </div>
    <div class="deskripsi">
        <p tabindex="0" class="rating-restaurant">Rating: ${restaurant.rating}</p>
        <p tabindex="0" class="city-restaurant" >${restaurant.city}</p>
        <h3 tabindex="0" class="food-name">${restaurant.name}</h3>
        <p tabindex="0" class="text-deskripsi">Lorem ipsum dolor sit, amet consectetur adipisicing elit. Sit, dolorum ratione debitis earum fugit veniam vitae consequatur corporis ullam cupiditate nam ad, fuga inventore quos velit beatae, voluptatibus aliquid sed.</p>
        <button tabindex="-1"><a id="detail-button" href="/#/detail/${restaurant.id}">Detail Restaurant</a></button> 
    </div>
`;

const createFavoriteButtonTemplate = () => `
    <button tabindex="0" id="favorite-button" class="favorite" type"submit">Add to favorite</button>    
`;

const createUnFavoriteButtonTemplate = () => `
    <button tabindex="0" id="favorite-button" class="un-favorite" type"submit">Remove to favorite</button>    
`;

export {
  createRestaurantsDetailTemplate,
  createRstaurantsItemTemplate,
  createFavoriteButtonTemplate,
  createUnFavoriteButtonTemplate,
};
