import FavoriteRestaurantsIdb from '../data/favorite-restaurants-idb';
import { createFavoriteButtonTemplate, createUnFavoriteButtonTemplate } from '../views/templates/template-creator';

const FavoriteButtonInitiator = {
  async init({ favoriteButtonContainer, restaurants }) {
    this._favoriteButtonContainer = favoriteButtonContainer;
    this._restaurantDetail = restaurants;
    await this._renderButton();
  },

  // render button add favorite/remove favorite
  async _renderButton() {
    const { id } = this._restaurantDetail;

    if (await this._isRestaurantExist(id)) {
      this._renderUnFavorite();
    } else {
      this._renderFavorite();
    }
  },

  async _isRestaurantExist(id) {
    const restaurants = await FavoriteRestaurantsIdb.getRestaurants(id);
    return !!restaurants;
  },

  async _renderFavorite() {
    this._favoriteButtonContainer.innerHTML = createFavoriteButtonTemplate();
    const favoriteButton = document.querySelector('#favorite-button');
    favoriteButton.addEventListener('click', async () => {
      await FavoriteRestaurantsIdb.putRestaurants(this._restaurantDetail);
      this._renderButton();
    });
  },

  async _renderUnFavorite() {
    this._favoriteButtonContainer.innerHTML = createUnFavoriteButtonTemplate();
    const unFavoriteButton = document.querySelector('#favorite-button');
    unFavoriteButton.addEventListener('click', async () => {
      await FavoriteRestaurantsIdb.deleteRestaurant(this._restaurantDetail.id);
      this._renderButton();
    });
  },

};

export default FavoriteButtonInitiator;
